import React, { Component } from 'react';
import WordCard from './WordCard';

const newsUrl = 'https://newsapi.org/v2/everything?q=peru&page=1&language=en&apiKey=';

export default class News extends Component {

	constructor(props) {
		super(props);

		this.state = { 
			articlesJson: '',
		};		
		this.fetchArticleJsonData = this.fetchArticleJsonData.bind(this);
	}

	componentDidMount() {
		this.fetchArticleJsonData();
	}

	fetchArticleJsonData() {
		let context = this;

		fetch(newsUrl + process.env.REACT_APP_GOOGLE_NEWS_API)
		  .then(function(response) {
		    return response.json();
		  })
		  .then(function(json) {
		    context.setState((state, props) => ({ articlesJson: json }) );
		  })
		  .catch(function(error) {
		  	console.log('fetch error: ' + error);
		  });
	}

	buildNewsCards(json) {
		
		if (json.articles === undefined) {
			return;
		}

		return json.articles.map(function(article) {
			return (
					<div key={ article.url }>
						<WordCard 
							header={ article.title } 
							body={ article.content } 
							link={ article.url } 
							linkText={ 'Read More...' }
							image={ article.urlToImage } />
					</div>
				);
		});
	}


	render() {
		return(
			<div>
				<div className='content-header-container'>
					<div className='content-header-inner'>
						<h2>News</h2>
						<p>Here is a list of recent articles relating to Peru, stay informed!</p>
					</div>
				</div>
				<div className='content'>
					{ this.buildNewsCards(this.state.articlesJson) }
				</div>
			</div>
			);
	}
}