import React, { Component } from 'react';
import './WordCard.css';

class WordCard extends Component {

	constructor(props) {
		super(props);

		this.getMedia = this.getMedia.bind(this);
		this.link = this.link.bind(this);
	}

	getMedia(type, value) {
		return <div></div>
	}

	link() {
		if (this.props.link !== undefined && this.props.link.length > 0) {
			return (
				<a className='link-text' href={ this.props.link } target='_blank' rel='noopener noreferrer'>
					<button>{ this.props.linkText !== undefined ? this.props.linkText : this.props.link }</button>
				</a>
			);
		}
	}

	image() {
		if (this.props.link !== undefined && this.props.link.length > 0) {
			return(
				<img src={ this.props.image }/>
			);
		}
	}

	render() {
		return(
				<div className='word-card'>
					<div className='word-card-inner'>
						<div className='word-card-content'>
							{ this.image() }
					  		<h2>{this.props.header}</h2>
					    	<p className={ this.props.centerContent ? 'center-content' : '' }>{this.props.body}</p>
					  		<br/>
					  		{ this.link() }
						</div>
					</div>
				</div>
		);
	}
}

export default WordCard;